.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: #d9e8f8;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #333;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff; /* White background for form */
  padding: 20px 30px;
  border-radius: 8px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.1); /* Light shadow */
  width: 350px;
  font-size: 1.2rem;
  margin-bottom: 50px;
}

form div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 10px 0;
}

form label {
  flex: 1;
  text-align: left;
  font-weight: bold;
  color: #333;
}

form input, form select {
  flex: 1.5;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1.0rem;
}

form input[type="radio"] {
  flex: 0; 
  width: auto;
  margin-left: 40px;
}

button {
  margin: 20px auto 0 auto;
  padding: 10px 20px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  display: block;
  font-size: 1.1rem;
}

button:hover {
  background-color: #0056b3; /* Darker shade on hover */
}


#msg{
  margin-top: 15px;
  text-align: center;
  color: #333;
}
